* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

nav {
  background-color: #fff;
  padding: 10px;
}

nav ul {
  text-align: center;
  list-style: none;
}

nav ul li {
  margin-right: 20px;
  display: inline-block;
}

nav ul li a {
  color: #000;
  text-decoration: none;
}

@media (width <= 600px) {
  nav ul {
    text-align: left;
  }

  nav ul li {
    margin-bottom: 10px;
    display: block;
  }
}
